import {
  Button,
  ListItem,
  Modal,
  ModalBody,
  ModalCloseButton,
  ModalContent,
  ModalFooter,
  ModalHeader,
  ModalOverlay,
  OrderedList,
  Text,
} from '@chakra-ui/react';

type ExplanationModalProps = {
  isOpen: boolean;
  onClose: () => void;
};

export const ExplanationModal = ({
  isOpen,
  onClose,
}: ExplanationModalProps) => {
  return (
    <Modal isOpen={isOpen} onClose={onClose} size='lg' isCentered>
      <ModalOverlay />
      <ModalContent>
        <ModalHeader>Instructions</ModalHeader>
        <ModalCloseButton />
        <ModalBody>
          <Text mb='25px'>
            Cette application est un gnrateur de combinaison alatoire.
          </Text>
          <OrderedList>
            <ListItem>
              Choisissez le nombre d'lment  gnrer alatoirement
            </ListItem>
            <ListItem>
              Rentrez des valeurs (spares par un passage  la ligne) dans
              chaque liste
            </ListItem>
            <ListItem>Cliquez sur le bouton</ListItem>
            <ListItem>
              Et vous voil avec une liste d'lments gnrs !
            </ListItem>
          </OrderedList>
        </ModalBody>
        <ModalFooter>
          <Button colorScheme='blue' mr={3} onClick={onClose}>
            Fermer
          </Button>
        </ModalFooter>
      </ModalContent>
    </Modal>
  );
};
